<template>
	<div class="examtheory">
		<p class="nav"><span class="blue">考试&试卷</span> > 编辑考试</p>
		<div class="content">
			<p class="top">
				<span>考试名称：{{examname}}</span>
				<span>课程：{{examcoursename}}</span>
				<span>考试类型：{{examtype|typefn}}</span>
				<span>考试形式：{{exammodel|modelfn}}</span>
			</p>
			<div class="bottom">
				<p class="cell_line">
					<span>考试名称:</span>
					<el-input v-model="examname" placeholder="请输入考试名称" class="right" style="padding: inherit;width: 350px;"></el-input>
				</p>
				<p class="cell_line">
					<span>课程:</span>
					<el-select v-model="examcourseid"  placeholder="请选择" class="right" style="padding: inherit;width: 350px;">
			            <el-option v-for="item in courselist" :key="item.id" :label="item.name" :value="item.id"> </el-option>
			        </el-select>
				</p>
				<p class="cell_line">
					<span>考试类型:</span>
					<el-select v-model="examtype" placeholder="请选择" class="right" style="padding: inherit;width: 350px;">
					    <el-option v-for="item in typelist" :key="item.value" :label="item.label" :value="item.value" > </el-option>
			        </el-select>
				</p>
				<p class="cell_line">
					<span>考试形式:</span>
					<label class="right box" style="border: none;">{{exammodel|modelfn}}</label>
				</p>
				<!--<p class="cell_line">
					<span>考试时间:</span>
					<el-date-picker v-model="time" @change="timechange" type="datetimerange" placeholder="选择时间范围" class="right" style="padding: inherit;width: 350px;"></el-date-picker>
				</p>-->
				<p class="cell_line">
					<span>考生范围:</span>
					<label class="right box"><p v-for="item in checkedStu">{{item.name}}</p> </label><el-button type="primary" class="addbtn" @click="dialogSelStu=true">选择</el-button>
				</p>
				<p class="cell_line">
					<span>考试试卷:</span>
					<label class="right box">{{selectpaperitem.name}}</label><el-button type="primary" class="addbtn" @click="selectpaper">选择</el-button>
				</p>
				<!--<p class="cell_line">
					<span>考试地点:</span>
					<label class="right box"><p v-for="item in siteselected">{{item.name}}</p></label><el-button type="primary" class="addbtn" @click="selectsite">选择</el-button>
				</p>-->
				<!--操作题-->
				<div class="borderbottom questioncontent" v-if="questionlist" v-for="(item,index) in questionlist">
					<p class="line">
						考试题目{{index+1}}:
						<label class="right box" >{{item.questionname}}</label>
					</p>
					<p class="line">
						考试起止时间:
						<el-date-picker v-model="item.questiontime" type="datetimerange" placeholder="选择时间范围" class="right" style="padding: 0;"></el-date-picker>
					</p>
					<p class="line">
						评卷老师:
						<el-select v-model="item.questionteacher" filterable placeholder="请选择" style="padding: 0;margin-left: 35px;"> 
							<el-option  v-for="teacheritem in teacherlist" :key="teacheritem.value" :label="teacheritem.label" :value="teacheritem.value"> </el-option>
						</el-select>
					</p>	
				</div>
				<p class="cell_line" style="text-align: center;">
					<el-button type="primary" @click="save">保存</el-button>
					<el-button @click="cancel">取消</el-button>
				</p>
			</div>
		</div>
		<!--选择考生-->
		<el-dialog  title="选择学生" :visible.sync="dialogSelStu" :close-on-click-modal="false" @close="">
            <translist :dirs ="transdirs" :data1 = "leftData" @getChildrens="getChildren" @clickDir = "clickDir" @checkedData = "getCheckedData"></translist>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogSelStu = false">取 消</el-button>
                <el-button type="primary" @click="submitCheck">确 定</el-button>
            </div>
        </el-dialog>
        <!--选择试卷-->
        <div class="popbox" v-show="selectpapershow">
			<p class="poptitle">选择试卷 <i class="el-icon-close" @click="closepop"></i></p>
			<p class="popcontent">
				<el-select v-model="courseid" placeholder="课程">
				    <el-option label="全部" value=""> </el-option>
				    <el-option v-for="item in courselist" :key="item.id" :label="item.name" :value="item.id"> </el-option>
				</el-select>
				<el-date-picker v-model="papertime" @change="papertimechange" type="daterange" align="right" placeholder="选择日期范围" :picker-options="pickerOptions"> </el-date-picker>
				<el-input class="searchbox" placeholder="输入试卷名" icon="search" v-model="papername" :on-icon-click="searchpaper"></el-input>
			</p>
			<div class="popcontent">
				<div class="table">
					<el-table @row-click="checkpaper" :data="paperlist" border style="width: 100%">
				      <el-table-column label="试卷名称">
				      	<template scope="scope">
				      		<div class="labelbox bgpurple" v-if="scope.row.type==1">理</div>
		      				<div class="labelbox bgblack" v-if="scope.row.type==2">操</div>
				      		<div class="labelbox bgorange" v-if="scope.row.type==5">随</div>
				      		<span>{{scope.row.name}}</span>
				      	</template>
				      </el-table-column>
				      <el-table-column prop="operatortime" label="操作时间"></el-table-column>
				    </el-table>
		    		<el-pagination class="pagebox" layout="prev, pager, next" :total="total" @current-change="handleCurrentChange" > </el-pagination>
				</div>
			</div>
			</p>
		</div>
		<!--考试地点-->
		<div class="popbox" style="width: 460px;height: 450px; left: 32%;margin: auto;" v-show="selectsiteshow">
			<p class="poptitle">选择教室 <i class="el-icon-close" @click="closepop"></i></p>
			<p class="popcontent">
				<el-transfer
				    filterable
				    :filter-method="filterMethod"
				    filter-placeholder="请输入楼栋或教室"
				    v-model="value"
				    :data="data">
				</el-transfer>
			</p>
			<p class="popcontent" style="text-align: center;margin-top: 20px;">
				<el-button type="primary" @click="sure">确认</el-button>
				<el-button @click="closepop">取消</el-button>
			</p>
		</div>
	</div>
</template>

<script>
import {post,setCookie,getCookie,setTitle} from '../../config/util'
import breadcrumb from '../../components/breadcrumb'
import translist from '../../components/translist'
import $ from 'jquery'
import moment from 'moment'
export default {
	
data() {
  	return {
		examid:this.$route.query.examid,//考试id
		examname:'',//考试名称
		examcourseid:'',//考试学科
		examcoursename:'',//考试学科
		examtype:'',//考试类型
		exammodel:'',//考试形式
		typelist:[{value:0,label:'单元考试'},{value:1,label:'期中考试'},{value:2,label:'期末考试'},{value:3,label:'随堂小测'}],
		time:[],//考试时间范围
		
  		starttime:'',//考试开始时间
        endtime:'',//考试结束时间
        selectpapershow:false,//选择试卷pop
        selectpaperitem:{
        	name:'',
        	paperid:'',
        },//已选试卷item
        papertime:'',//查询试卷时间
  		currentpage:1,//当前页码
		pagesize:10,//每页条数
  		total:0,//数据总数
  		paperlist:[],//试卷列表
        courselist:[],//课程选择
        courseid:'',//选择课程id
        paperstartime:'',//试卷开始时间
        paperendtime:'',//试卷技术时间
        papername:'',//试卷名
  		pickerOptions: {//试卷操作时间快捷
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        dialogSelStu:false,//选择学生pop
		transdirs:[{name:'所有考生',id:0}],
		checkedStu:[],//已选学生列表
        leftData:[],//
        classList:[],//教室列表
  		selectsiteshow:false,//教室pop
		data:[],//教室pop数据
        value: [],//已选教室id列表
        filterMethod(query, item) {//教室查询
          return item.sites.indexOf(query) > -1;
        },
		radio:'',//选择考试模式
        sitelist:[],//教室列表
        siteselected:[],//已选教室列表
        questionlist:[],//题目列表
        teacherlist:[],//教师列表
  	}
},
components: {
    breadcrumb,
    translist,
},
filters:{
	typefn:function(value){
		var status=value;
		switch (value){
			case 0: status="单元考试";
				break;
			case 1: status="期中考试";
				break;
			case 2: status="期末考试";
				break;
			case 3: status="随堂小测";
				break;
			default:
				break;
		}
		return status;
	},
	modelfn:function(value){
		var status=value;
		switch (value){
			case 0: status="理论笔试";
				break;
			case 1: status="理论机试";
				break;
			case 2: status="操作考试";
				break;
			default:
				break;
		}
		return status;
	}
},
mounted(){
//	if(this.row.model=='理论机试'){
//		this.radio=0
//	}
	this.getAllClass();
	this.queryCourse();
	this.queryteacherlist();
	this.queryexamdetail();
},
methods:{
	queryexamdetail(){//查询考试详情
		var self=this;
		post('/unifyexam/queryexamdetail',{
            command:"unifyexam/queryexamdetail",
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            teacherid:getCookie('uid'),
            examid:self.examid
        }).done((res)=>{
            if(res.errcode==0){
            	self.examname=res.name;
				self.examcoursename=res.coursename;
				self.examtype=res.type; 
				self.exammodel=res.model;
            	self.examcourseid=res.courseid;
//          	self.time.push(res.starttime,res.endtime);
            	self.selectpaperitem.name=res.papername;
				self.selectpaperitem.paperid=res.paperid;
				for (var i=0;i<res.classlist.length;i++) { 
					var obj={
						ctype:'1',
						name:res.classlist[i].classname,
						id:res.classlist[i].classid
					}
					self.checkedStu.push(obj)
				}
//				for(var i=0;i<res.placelist.length;i++){
//					var obj={
//						name:res.placelist[i].placename,
//						id:res.placelist[i].placeid
//					}
//					self.siteselected.push(obj)
//				}
				for (var i=0;i<res.iteamteacherlist.length;i++) { 
					var obj={
						questionname:res.iteamteacherlist[i].questionname,
						questiontime:[res.iteamteacherlist[i].starttime,res.iteamteacherlist[i].endtime],
						questionteacher:res.iteamteacherlist[i].teacherid,
						questionid:res.iteamteacherlist[i].questionid
					}
					self.questionlist.push(obj)
				}
            }
        })
	},
	
	queryCourse(){//查询学科列表
        var self=this;
		post('/cls/selectCourses',{
            command:"cls/selectCourses",
            sessionid:getCookie('sid'),
            loginid:getCookie('uid')
        }).done((res)=>{
            if(res.errcode==0){
                self.courselist=res.courseslist;
            }
        })
	},
	querysite(){//查询场地
		var self=this;
		post('/site/listplace',{
            command:"site/listplace",
			sessionid:getCookie('sid'),
			loginid:getCookie('uid'),
			pagenum:1,
			pagepersize:1000,
        }).done((res)=>{
            if(res.errcode==0){
				var sites=[];
				self.sitelist=res.placelist;
				var id=[];
				for (var i=0;i<res.placelist.length;i++) {
					sites.push(res.placelist[i].name)
					id.push(res.placelist[i].id)
				}
				sites.forEach((site, index) => {
			      self.data.push({
			        label: site,
			        key: id[index],
			        sites: sites[index]
			      });
			  });
            }
        })
	},
	querypaper(currentpage,pagesize,courseid,starttime,endtime,name){//查询试卷
		var self=this;
		post('/exampaper/querypaper',{
            command:"exampaper/querypaper",
			sessionid:getCookie('sid'),
			loginid:getCookie('uid'),
			page:currentpage,
			reqnum:pagesize,
			majorid:courseid,
			name:name,
			type:6,
			starttime:starttime,
			endtime:endtime
        }).done((res)=>{
            if(res.errcode==0){
                self.paperlist=res.paperlist;
                self.total=res.totalcount;
            }
        })
	},
	/**************************************选择考生*******************************************/ 
    moveFile(pid,cids){
        let self = this;
        post('/courseware/movefiles',{
            uid:getCookie('uid'),
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            command:"courseware/movefiles",
            parent_id:pid,
            idlist:cids
        }).done((data)=>{
            if(data.errcode==0){
                self.$message({
                    type:'success',
                    message:'移动成功'
                });
                self.dialogMove = false;
                self.getFiles({page:1});
            }else{
                self.$message.error('移动失败');
            }
        })
    },
    getChildren(item){
        let self = this;
        if(item.ctype!==undefined){
            post('/cls/listsclassStd',{
                command:'cls/listsclassStd',
                sessionid:getCookie('sid'),
                loginid:getCookie('uid'),
                id:item.id,
            }).done((data)=>{
                if(data.errcode==0){
                    self.leftData = data.stdlist;
                    _(self.checkedStu).forEach((it)=>{
                        let temp = _.find(self.leftData,{id:it.id,name:it.name});
                        temp &&(self.$set(temp,'checked',true));
                    });
                    _(self.leftData).forEach((it)=>{
                        it.end = true;
                    })
                }
            })
        }


    },
    clickDir(item,index){
        let self = this;
        if(index==0){
            this.leftData=this.classList;
            _(this.leftData).forEach((it)=>{
                it.checked = false;
            })
            _(this.checkedStu).forEach((it)=>{
                let temp = _.find(this.leftData,{id:it.id,name:it.name});
                temp &&(temp.checked = true);
            })
        }
        this.transdirs.splice(index+1,100);
    },
    submitCheck(){
        if(this.checkedStu.length==1){
            this.checkedInfo = this.checkedStu[0].name;
        }else if(this.checkedStu.length==2){
            this.checkedInfo = this.checkedStu[0].name+'、'+this.checkedStu[1].name;
        }else if(this.checkedStu.length>2){
            this.checkedInfo = this.checkedStu[0].name+'、'+this.checkedStu[1].name+'等';
        }else{
            this.checkedInfo='';
        }
        this.dialogSelStu = false;
    },
    getCheckedData(data){
        this.checkedStu = data;
    },
    getAllClass(){
        let self = this;
        post('/cls/listsclassbytid',{
            command:'cls/listsclassbytid',
            sessionid:getCookie('sid'),
            loginid:getCookie('uid'),
            teacherid:getCookie('uid')
        }).done((data)=>{
            if(data.errcode==0){
                self.classList = data.sclassList;
                self.leftData = data.sclassList;
            }
        })
    },
    /*****************************************选择试卷**************************************************/
    closepop(){//关闭弹窗
		this.selectpapershow=false;
		this.selectsiteshow=false;
	},
	timechange(val){//时间范围
		this.starttime=val.slice(0,19);
		this.endtime=val.slice(22,41);
	},
	selectpaper(){//打开选择试卷pop
		this.selectpapershow=true;
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	handleCurrentChange(val) {//试卷分页
	    this.currentpage=val;
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	papertimechange(val){//试卷时间查询
		this.paperstarttime=val.slice(0,11);
		this.paperendtime=val.slice(12,23);
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	searchpaper(){//试卷名查询
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	checkpaper(row){//选择试卷
		this.selectpaperitem.name=row.name;
		this.selectpaperitem.paperid=row.paperid;
		this.selectpapershow=false;
		this.selectlist(row.paperid)
	},
	selectlist(paperid){//查询试卷题目
        var self=this;
        self.questionlist=[];
		post('/exampaper/queryselectedquestion',{
            command:'exampaper/queryselectedquestion',
          	sessionid: $.cookie('sid'),
          	loginid:$.cookie('uid'),
          	paperid:paperid,
          	questiontypeid:7
        }).done((res)=>{
            if(res.errcode==0){
//          	self.questionlist=res.questionlist;
            	for (var i=0;i<res.questionlist.length;i++) {
            		self.questionlist.push({
            			questionname:res.questionlist[i].questionname,
            			questionid:res.questionlist[i].questionbaseid,
            			questiontime:'',
            			questionteacher:''
            		})
            	}
            	
            }
        })
   },
	queryteacherlist(){//查询授课教师
        var self=this;
		post('/cls/teacherlist',{
            command:'cls/teacherlist',
          	sessionid: $.cookie('sid'),
          	loginid:$.cookie('uid'),
          	roleid:3,
        }).done((res)=>{
            if(res.errcode==0){
            	for (var i=0;i<res.teacherlist.length;i++) {
            		self.teacherlist.push({
            			label:res.teacherlist[i].teacherame,
            			value:res.teacherlist[i].teacherid
            		})
            	}
            }
        })
    },
    /*****************************************选择场地**************************************************/
	selectsite(){
		this.selectsiteshow=true;
		this.querysite();
	},
	sure(){//确定场地选择
		var self=this;
		for (var i=0;i<self.value.length;i++) {
			for(var j=0;j<self.sitelist.length;j++){
				if (self.value[i]==self.sitelist[j].id) {
					self.siteselected.push({
						name:self.sitelist[j].build+self.sitelist[j].floor+self.sitelist[j].name,
						id:self.sitelist[j].id
					})
				}
			}
		}
		this.selectsiteshow=false;
	},
	save(){//保存考试
        let self = this;
        var stuarr=[];console.log(JSON.stringify(self.checkedStu))
        for (var i=0;i<self.checkedStu.length;i++) {
        	stuarr.push(self.checkedStu[i].id)
        }
        if (self.startime==''&self.endtime=='') {
        	this.starttime=self.time.slice(0,19);
			this.endtime=self.time.slice(22,41);
        }
        if(self.value.length==0){
        	for (var i=0;i<self.siteselected.length;i++) {
        		self.value.push(self.siteselected[i].id)	
        	}
        }
        var itemteacherlist=[];
        for (var i=0;i<self.questionlist.length;i++){
			if(self.questionlist[i].questiontime){
	        	itemteacherlist.push({
	        		teacherid: self.questionlist[i].questionteacher,
	        		starttime: moment(self.questionlist[i].questiontime[0]).format('YYYY-MM-DD HH:mm:ss'),
	        		endtime: moment(self.questionlist[i].questiontime[1]).format('YYYY-MM-DD HH:mm:ss'),
	        		questionid: self.questionlist[i].questionid,
	        	})
	        }else{
	        	itemteacherlist.push({
	        		teacherid: self.questionlist[i].questionteacher,
	        		starttime: '',
	        		endtime: '',
	        		questionid: self.questionlist[i].questionid,
	        	})
	        }
       }
        post('/unifyexam/modifyexam',{
            command:'unifyexam/modifyexam',
            sessionid:getCookie('sid'),
            loginid:getCookie('uid'),
            teacherid:getCookie('uid'),
            examid:self.examid,
            name:self.examname,
            courseid:self.examcourseid,
            model:self.exammodel,
            type:self.examtype,
            starttime:self.starttime,
            endtime:self.endtime,
            answermode:self.radio,
            paperid:self.selectpaperitem.paperid,
            classlist:stuarr,
            placelist:self.value,
            itemteacherlist:itemteacherlist,
        }).done((data)=>{
            if(data.errcode==0){
                this.$router.push({
			      name:'examManage',
			      query:{
			      	
			      }
			    })
            }
        })
	},
	cancel(){//取消新增试卷
		history.go(-1)
	}
},
watch:{
	courseid:function(val){
		this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	}
}

}
</script>

<style scoped>
.nav{
	font-size: 13px;
	margin: 5px 0;
}
.blue{
	color:#20a0ff;
}
.content{
	border: 1px solid #bfcbd9;
}
.top{
	border-bottom: 1px solid #bfcbd9;
	padding: 25px 30px;
}
.top span{
